import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {Button, Table, Typography, Tooltip} from 'antd'
import {DeleteOutlined, MessageOutlined,HourglassOutlined} from '@ant-design/icons'
import moment from "moment";


const RoomsActiveTable = ( props )=> {
    const columns = [
        {
            key:"no",
            title:"No",
            width:60,
            render: (_,val,index)=> index + 1
        },
        {
            key:'room',
            title:"Room",
            render:(_,val,index)=> <Typography.Text>Rooms Name</Typography.Text>
        },
        {
            key:"user",
            title:"User",
            render:(_,val,index)=> <Typography.Text>User</Typography.Text>
        },
        {
            key: "date",
            title: "Start Date",
            render:()=> {
                return (
                    <div className="w-full">
                        <Typography.Text>{moment().format('DD MMMM YYYY')}</Typography.Text>
                    </div>
                )
            }
        },
        {
            key: "expires-in",
            title: "Expires In",
            render:()=> {
                let startTime = moment()
                let end = moment(moment().add(100,'day').format("DD MM YYYY"),'DD MM YYYY').utc()
                var duration = moment.duration(end.diff(startTime));
                var hours = duration.asDays();
                return (
                    <div className="w-full">
                        <Typography.Text>{hours}</Typography.Text>
                    </div>
                )
            }
        },
        {
            key:"action",
            title:"",

            render:(_,val,index)=> {
                return (
                    <div className="text-right">
                        <Tooltip title={'Send Message to User'}>
                            <Button
                                type={'primary'}
                                ghost
                                size={'small'} shape={'circle'} icon={<MessageOutlined/>} className={'ml-2'}/>
                        </Tooltip>
                        <Tooltip title={'Room is Ready ?'}>
                            <Button
                                type={'primary'}
                                ghost
                                size={'small'} shape={'circle'} icon={<HourglassOutlined />} className={'ml-2'}/>
                        </Tooltip>

                    </div>
                )
            }
        },

    ]
    return (
        <Table
            dataSource={[{},{}]}
            columns={columns}
        />
    )
}

RoomsActiveTable.propTypes = {}
RoomsActiveTable.defaultProps = {}

export default connect(()=> {
    return {}
},{})(React.memo(RoomsActiveTable))